import "regenerator-runtime"; /* for async await transpile */
import "../styles/main.css";
import "./component/navbar.js";
import "./component/jumbotron.js";
import "./component/footer.js";
import App from "./views/app";

document.addEventListener("DOMContentLoaded", async () => {
  const Data = await import("../DATA.json");
  const datas = Data.default.restaurants;
  let dataCard = "";
  datas.forEach((data) => {
    dataCard += `
       <div class="card">
           <img class="pict" src="${data.pictureId}" alt="${data.name}">
           <div class="city">${data.city}</div>
           <div class="content_card">
           <p tabindex="0" class="rate"> Rating : ${data.rating}</p>
               <p tabindex="0"  class="title">${data.name}</p>
               <div tabindex="0" class="desc">${data.description.slice(
                 0,
                 500
               )}...</div>
           </div>
       </div>
       `;
  });
  document.querySelector("#list").innerHTML = dataCard;
});

const app = new App({
  button: document.querySelector("nav ul"),
  drawer: document.querySelector(".line"),
  content: document.querySelector("main"),
});
